











import { Component, Vue, Watch } from "vue-property-decorator";
import { Action, Getter } from "vuex-class";
@Component({
  components: {},
})
export default class Home extends Vue {
  @Action getData!: () => any;
  @Getter listPost!: any;
  value: string = "";

  @Action("insertPost")
  insertPost(val: string) {
    this.insertPost(val);
  }
  created() {
    this.getData();
  }

  @Action("deletePost")
  deletePost(id: any) {
    this.deletePost(id);
  }
  @Watch("listPost")
  wlistPost() {
    this.getData();
    this.value = "";
  }
}
